import {store} from "../../store";
import {Col, Row, Button, Form, Input, message, Modal, Table, Popconfirm, Tag, Space} from "antd";
import {PlusOutlined} from '@ant-design/icons';
import * as event from "../../event";
import React from 'react'
import * as g from "../../g";
import * as util from '../../util'


export const EntityLabel = (props) => {
    const state = store.useContext()
    const [showModal, setShowModal] = React.useState(false)
    const [modalInit, setModalInit] = React.useState({})
    const columns = [
        {
            title: '',
            dataIndex: 'a',
            key: 'a',
            ...util.getColSearchLocal('App', 'name')
        }, {
            title: '',
            dataIndex: 'q',
            key: 'q',
            render: ((text, record) => {
                return text.substr(0, 50) + '...'
            })
        }, {
            title: '',
            key: 'action',
            render: (text, record) => {
                return <Button danger onClick={(e) => e.stopPropagation()}></Button>
            }
        }
    ]
    return (
        <>
            <Table
                columns={columns}
                dataSource={[
                    {
                        a: 'AI',
                        q: 'AICAIFirmenichArla FoodsAI150',
                        entity: ['', 'SmartProteins', '', ''],
                        label: ['', '']
                    }, {
                        a: '',
                        q: 'FirmenichAIAIFirmenichSmartProteins',
                        entity: ['', 'SmartProteins', '', ''],
                        label: ['', '']
                    }, {
                        a: '',
                        q: '04Q2T2. ~~DataFunFlinkDataFunTalk ',
                        entity: ['', 'FAISS', 'Airbnb', 'DataFunTalk'],
                        label: ['', '']
                    }, {
                        a: '',
                        q: '18Top1. Q2IQ2C',
                        entity: ['', 'SmartProteins', '', ''],
                        label: ['', '', '']
                    }
                ]}
                size={'small'}
                scroll={{y: 750}}
                bordered
                onRow={(record) => {
                    return {
                        onClick: (event) => {
                            setModalInit({
                                _id: record._id,
                                ask: record.a,
                                enter: record.q,
                                entity: record.entity,
                                label: record.label
                            })
                            setShowModal(true)
                        }
                    }
                }}
            />
        </>
    )
}

const EditEntityLabel = (props) => {
    const [form] = Form.useForm()
    form.setFieldsValue(props.initData)
    return (
        <>
            <Modal key={Math.random()}
                   width={750}
                   title={''}
                   visible={props.show}
                   okText={''} cancelText={''}
                   onCancel={(e) => {
                       props.setShow(false)
                       form.resetFields()
                   }}
                   onOk={() => {
                   }}
            >
                <Form key={Math.random()} form={form}>
                    <Form.Item label={''} name={'ask'} rules={[
                        {
                            required: true,
                            message: 'is required!',
                        }
                    ]}>
                        <Input/>
                    </Form.Item>
                    <Form.Item label={''} name={'enter'} rules={[
                        {
                            required: true,
                            message: 'is required!',
                        }
                    ]}>
                        <Input.TextArea rows={4}/>
                    </Form.Item>

                    <Form.Item label={''} name={'entity'}>
                        <EditableTagGroup name={''} tags={form.getFieldValue('entity')}/>
                    </Form.Item>
                    <Form.Item label={''} name={'label'}>
                        <EditableTagGroup name={''} tags={form.getFieldValue('label')}/>
                    </Form.Item>
                </Form>
            </Modal>
        </>
    )
}

const EditableTagGroup = (props) => {
    const [showInput, setShowInput] = React.useState(false)
    return (
        <Space>
            {props.tags.map((item) => {
                return <Tag closable>{item}</Tag>
            })}
            <span>
                {
                    showInput ? <Input onBlur={(e) => {
                        setShowInput(false)
                    }}/> : <Tag onClick={(e) => {
                        setShowInput(true)
                    }} className="site-tag-plus">
                        <PlusOutlined/> {`${props.name}`}
                    </Tag>
                }
                </span>
        </Space>
    )
}